[id="spotlight"] {
    position: relative;
    margin-bottom: 0;
    background-color: var(--ssgr);
    background-image: url(../svg/comb-back.svg), linear-gradient(60deg, var(--ssgr) 20%, var(--stgr) 100%);
    background-blend-mode: soft-light;
    background-size: contain;
    width: 100%;
    h2 {
        max-width: unset;
        text-align: center;
        color: var(--white);
        padding-bottom: 1rem;
    }
    [id="spotlight-boxes"] {
        display: flex;
        flex-direction: column;
        @media screen and (min-width: 960px) {
            flex-direction: row;
            justify-content: space-around;
            align-content: center;
        }
        .socialbox {
            background: var(--white);
            border-radius: 4px;
            border-bottom: 4px solid var(--hgob);
            overflow: hidden;
            margin-bottom: 1rem;
            flex: 1;
            &:last-child {
                margin-bottom: 0;
            }
            @media screen and (min-width: 960px) {
                //                justify-content: center;
                //                align-content: center;
                margin-bottom: 0;
                margin-right: 1rem;
                &:last-child {
                    margin-right: 0;
                }
            }
            a {
                display: block;
                color: var(--ssgr);
                background-size: 40%;
                padding: 2rem;
                text-decoration: none;
                height: 100%;
                background-repeat: no-repeat;
                background-position: bottom right;
                background-color: var(--spotbt); // background: linear-gradient(-20deg, var(--spotLt) 0%, var(--spotLt) 35%, var(--hgob) 100%); // url(../images/comb-pattern-1.svg),
                background-blend-mode: multiply;
                &:visited {
                    background-color: inherit;
                }
                &:hover,
                &:focus {
                    background-color: var(--spotlt);
                }
            }
        }
    }
}